import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";

const renderField = ({
  input,
  label,
  type,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormHistoryUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [
        {
          id: 0,
          name: "Cobol",
        },
        {
          id: 1,
          name: "JavaScript",
        },
        {
          id: 2,
          name: "Basic",
        },
        {
          id: 3,
          name: "PHP",
        },
        {
          id: 4,
          name: "Java",
        },
      ],
    };
  }
  handleOnSelect = (item) => {
    this.props.change("userID", item.name);
  };
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-4 d-none">
            <Field
              name="userID"
              label="User ID"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-lg-4">
            <label htmlFor="">User ID</label>
            <select name="" id="" className="form-control">
              <option value="DEFAULT" key="DEFAULT">
                DEFAULT
              </option>
            </select>
          </div>
          <div className="col-lg-4">
            <Field
              name="startDate"
              label="Tanggal Mulai"
              component={renderField}
              type="date"
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="endDate"
              label="Sampai Dengan"
              component={renderField}
              type="date"
            />
          </div>
          <div className="col-lg-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}
FormHistoryUser = reduxForm({
  form: "FormHistoryUser",
  enableReinitialize: true,
})(FormHistoryUser);
export default connect()(FormHistoryUser);
